.wrapper {
  font-size: 1.6rem;
  display: inline-block;
  line-height: 22px;
  border-radius: 4px;
  user-select: none;
  padding: 6px 8px;
}

.default {
  background: transparent;
}

.black {
  color: #000;
  font-weight: 600;
}

.blackOpacity {
  color: #161823;
}

.white {
  color: #fff;
  font-weight: 600;
}

.hoverPrimaryColor:hover {
  background: #fe2c550f;
}

.colorPrimary {
  color: #fe2c55;
  font-weight: 600;
}

.m {
  height: 36px;
}

.ls {
  min-width: 164px;
  height: 46px;
}

.l {
  width: 100%;
  height: 48px;
  display: block;
}

.xxl {
  width: 90%;
  height: 48px;
  display: block;
  margin: 0 auto;
}

.sm {
  min-width: 88px;
  line-height: 22px;
  height: 28px;
}

.sl {
  width: 60%;
}

.ssl {
  width: 200px;
}

.xl {
  min-width: 124px;
  max-width: 148px;
  height: 36px;
}

.textCenter {
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.primary {
  background-color: #fe2c55;
}

.hover {
  &:hover {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
  }
}

.marginLeft {
  margin-left: 16px;
}

.marginTop {
  margin-top: 20px;
}

.borderOpacity {
  border: 1px solid rgba(22, 24, 35, 0.12);
}

.border {
  border: 1px solid #fe2c55;
}

.red {
  color: #fe2c55;
}

.underline {
  &:hover {
    text-decoration: underline;
  }
}

.floatRight {
  float: right;
}

.actived {
  color: #161823;
  border: 1px solid rgba(22, 24, 35, 0.12);
}

.bgPrimary {
  background: rgba(22, 24, 35, 0.06);
}

.colorGrey {
  color: rgba(22, 24, 35, 0.34);
  font-weight: 400;
}
